import './menu.css'
import Logo from './Img/EyeCog_Logo.svg';
import { Link } from 'react-router-dom';

function Menu ({test}) {
    return (
      <div className='startmenu'>
          <div className="logo">
            <img src={Logo} alt="logo" width="150vw" />
            <p className='color'>Async</p><p>hrone</p>
         </div>
        <div className="menu">
            <ul>
              <li><Link to="/">Home</Link></li>
              <li><Link to="/kontaktformular">Kontaktformular</Link></li>
              <li><Link to="/about">Kontakt</Link></li>
              <li><Link to="/about">About</Link></li>
              <li>{test}</li>
            </ul>
        </div>
        </div>
    );
}

export default Menu;